@mixin font($font_name, $file_name, $weight, $style) {

  @font-face {
  font-family: $font_name;
  font-display: swap;
  src: url("../fonts/#{$file_name}.woff") format("woff"), url("../fonts/#{$file_name}.woff2") format("woff2");
  font-weight: #{$weight};
  font-style: #{$style};
  }
  }

  body{
    margin: 0;
    font-family: 'Arial';
  }
  
  .redact h3{
    
  }
  .container{
    max-width: 1200px;
    margin: 0 auto;
    padding:0 14px;
  }
  .background {
    background-color:#F7F7F7FF ;
    padding-top: 30px;
  }
  .main-title {
    margin: 0;
    line-height: 0;
    font-size: 27px;
    padding-top: 26px;
    margin-bottom: 27px;
  }
  .sub-title{
    margin: 0;
    line-height: 0;
    margin-bottom: 45px;
    font-size: 22px;
    color: #000000FF;
  }
  .logo {
    font-size: 13px;
    margin: 0;
    margin-bottom: 18px;
    font-weight: 100;
    color: #6A6A6AFF;
  }
  .card-input {
   font-size: 12px;
   margin-bottom: 32px;
   padding-left: 14px;
   width: 100%;
   height: 50px;
  }
  .insert-inner {
    display: flex;
    margin-bottom: 43px;
    @media (max-width:720px) {
