  }
  .input-text {
    margin-bottom: 39px;
  }
  .insert {
    border: 1px dashed;
    border-color: #6A6A6AFF;
    border-radius: 10px;
    max-width: 430px;
    height: 123px;
    font-size: 13px;
    color:#6A6A6AFF ;
    background-color: white;
    text-align: center;
    margin-right: 40px;
