  }
  .buttton { 
  }
  .bulleted-list {
  margin: 0;
  font-size: 13px;
  color:#6A6A6AFF;
  }
  .color-card{
    font-size: 22px;
    margin-bottom: 45px;
    margin: 0;
  }
