  .input-colorr {
    display: flex;
  }
  
  .hat-background {
  }
  .input-color, .input-color2 {
    width: 52px;
    height: 57px;
    border-style: none;
    padding: 0;
    border: none;
    margin: 0;
    background-color: transparent;
    transform: translateY(-4px);
  }
  .input-color2 {
    
  }
  .input-color-text{
    width:370px ;
    height: 48px;
    margin-bottom: 45px;
  }
  .page-background {
  }
  .stamp {
  }
  .numberofstamps {
    font-size: 22px;
  }
  .number-stamp {
    display: flex;
    flex-wrap: wrap;
    transform: translateX(-15px);
  }
  .number{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    width: 45px;
    height: 45px;
    border: 1px solid;
    margin-left: 15px;
    margin-bottom: 10px;
    &.active{
      border: none;
      background-color: #F5FF00;
    }
  }
  .number + .number{
    
  }
  .buttons{
    display: flex;
  }
  .input__wrapper {
    width: 100%;
    position: relative;
    display: flex;
    align-items: center;
    
  }
   
  .input__file {
    opacity: 0;
    visibility: hidden;
    position: absolute;
  }
   
  .input__file-icon-wrapper {
    height: 60px;
    width: 60px;
    margin-right: 15px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    border-right: 1px solid #fff;
  }
   
  .input__file-button-text {
   
  }
   
  .input__file-button {
    display: block;
    width: 100%;
    max-width: 135px;
    height: 50px;
    background: black;
    color: #fff;
    font-size: 20px;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    margin: 0;
    margin-left: 20px;
  }
  .appearance {
    margin-top: 60px;
  }
  .buttons {
    padding-top: 70px;
    padding-left: 13px;
    padding-bottom: 65px;
  }
  .button1 {
    padding-right: 27px;
  }
  .backе {
    width: 152px;
    height: 37px;
    font-size: 13px;
    background-color: white;
    border-style: solid;
    border-color: black;
    border-width: 1px;
  }
  .step {
    width: 152px;
    height: 55px;
    font-size: 13px;
    background-color: white;
    border-style: solid;
    border-color: black;
    border-width: 1px;
  }
 
  .step:hover{
    background-color: black;
    color: white;
  }
  .logo-inner{
    display: flex;
    margin-right: auto;
    font-size: 14px;
    @media (max-width:380px) {
      margin-left: 10px;
    }
  }
  .input-color-text1, .input-color-text2{
    width:370px ;
    height: 48px;
    margin-bottom: 45px;
  }
  .form {

		// .form__tab

		&__tab {
      display: flex;
      height: 50px;
      border: 1px solid black;
      max-width: 500px;
      justify-content: space-between;
      cursor: pointer;
		}

		// .form__tab-img

		&__tab-img {
      border-right: 1px solid black;
      width: 60px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      img{
        height: 50px;
      }
		}

		// .form__tab-name-inenr

		&__tab-name-inenr {
      display: flex;
      align-items: center;
		}

		// .form__tab-name

		&__tab-name {
		}

		// .form__tab-arrow

		&__tab-arrow {
      border-left: 1px solid black;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
		}
    &__tab-content{
      max-width: 500px;
      display: none;
      &.active{
        display: grid;
        grid-template-columns: 1fr 1fr;
        
      }
    }
}
.form__tab-inner{
  padding-bottom: 100px;
}

.form {

  // .form__botton-inner

  &__botton-inner {
    display: flex;
  }

  // .form__button

  &__button {
    
    width: 170px;
    height: 40px;
    border: 1px solid black;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 15px;
    &:hover{
      background-color: black;
      color: #fff;
    }
    &:hover .form__but{
      color: #fff;
    }
  }
  
  // .form__but

  &__but { 
    text-decoration: none;
    color: black;
    &:hover{
      color: #fff;
    }
  }
}
.form__button + .form__button{
  margin-left: 30px;
}

.drop-zone {
  width: 400px;
  height: 130px;
  padding: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-weight: 500;
  font-size: 20px;
  cursor: pointer;
  color: #cccccc;
  border: 1px dashed gray;
  border-radius: 10px;
  margin-right: 30px;
  @media (max-width:460px) {
    width: 100%;
  }
}

.drop-zone--over {
  border-style: solid;
}

.drop-zone__input {
  display: none;
}

.drop-zone__thumb {
  width: 100%;
  height: 100%;
  border-radius: 10px;
  overflow: hidden;
  background-color: #cccccc;
  background-size: cover;
  position: relative;
}

.drop-zone__thumb::after {
  content: attr(data-label);
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 5px 0;
  color: #ffffff;
  background: rgba(0, 0, 0, 0.75);
  font-size: 14px;
  text-align: center;
}


@import '_null';
